import React, { useState, useEffect } from 'react';
import { Helmet } from 'react-helmet-async';
import Navbar from '../../components/navbar';
import Footer from '../../components/footer';
import { API_BASE_URL, getAuthHeaders } from '../../config/api.config';
import './styles.css';

const Profile = () => {
  const [user, setUser] = useState(null);
  const [savedPrograms, setSavedPrograms] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        setError(null);
        await Promise.all([fetchUserData(), fetchSavedPrograms()]);
      } catch (error) {
        setError(error.message);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  const fetchUserData = async () => {
    try {
      const response = await fetch(`${API_BASE_URL}/users/me`, {
        headers: getAuthHeaders()
      });
      
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Eroare la obinerea datelor utilizatorului');
      }
      
      const data = await response.json();
      setUser(data);
    } catch (error) {
      console.error('Eroare la obinerea datelor utilizatorului:', error);
      throw error;
    }
  };

  const fetchSavedPrograms = async () => {
    try {
      const response = await fetch(`${API_BASE_URL}/api/saved-programs`, {
        headers: getAuthHeaders()
      });
      
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Eroare la obinerea programelor salvate');
      }
      
      const data = await response.json();
      setSavedPrograms(data);
    } catch (error) {
      console.error('Eroare la obinerea programelor salvate:', error);
      throw error;
    }
  };

  const handleRemoveSavedProgram = async (programId) => {
    try {
      const response = await fetch(`${API_BASE_URL}/api/saved-programs/${programId}`, {
        method: 'DELETE',
        headers: getAuthHeaders()
      });
      
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Eroare la eliminarea programului din lista de programe salvate');
      }
      
      setSavedPrograms(savedPrograms.filter(sp => sp.program.id !== programId));
    } catch (error) {
      console.error('Eroare la eliminarea programului:', error);
      setError(error.message);
    }
  };

  if (loading) {
    return (
      <div className="profile-container">
        <div className="loading">Se ncarc...</div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="profile-container">
        <div className="error">
          <p>Eroare: {error}</p>
          <button onClick={() => window.location.reload()}>Rencarc pagina</button>
        </div>
      </div>
    );
  }

  return (
    <div className="profile-container">
      <Helmet>
        <title>Profil - Study In Moldova</title>
        <meta property="og:title" content="Profil - Study In Moldova" />
      </Helmet>
      <Navbar />
      <div className="profile-content">
        <h1>Profilul meu</h1>
        
        {user && (
          <div className="user-info">
            <h2>Informaii personale</h2>
            <p><strong>Nume:</strong> {user.name}</p>
            <p><strong>Email:</strong> {user.email}</p>
            {user.phone && <p><strong>Telefon:</strong> {user.phone}</p>}
            {user.country_of_origin && <p><strong>ara de origine:</strong> {user.country_of_origin}</p>}
            {user.nationality && <p><strong>Naionalitate:</strong> {user.nationality}</p>}
          </div>
        )}

        <div className="saved-programs">
          <h2>Programe salvate</h2>
          {savedPrograms.length > 0 ? (
            <div className="programs-grid">
              {savedPrograms.map((savedProgram) => (
                <div key={savedProgram.program.id} className="program-card">
                  <div className="program-header">
                    <h3>{savedProgram.program.name}</h3>
                    <button
                      className="remove-button"
                      onClick={() => handleRemoveSavedProgram(savedProgram.program.id)}
                    >
                      Elimin
                    </button>
                  </div>
                  <div className="program-details">
                    <p><strong>Universitate:</strong> {savedProgram.program.University?.name || 'N/A'}</p>
                    <p><strong>Facultate:</strong> {savedProgram.program.faculty}</p>
                    <p><strong>Grad:</strong> {savedProgram.program.degree}</p>
                    <p><strong>Credite:</strong> {savedProgram.program.credits}</p>
                    <p><strong>Limbi:</strong> {Array.isArray(savedProgram.program.languages) ? savedProgram.program.languages.join(', ') : savedProgram.program.languages}</p>
                    <p><strong>Durat:</strong> {savedProgram.program.duration}</p>
                    <p><strong>Tax de colarizare:</strong> {savedProgram.program.tuitionFee}</p>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <p>Nu avei programe salvate.</p>
          )}
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default Profile;